import { useNavigate } from "react-router-dom";

import Card from "../UI/Card";
import classes from "./NavBar.module.css";
import PersonIcon from "@mui/icons-material/Person";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { useDispatch } from "react-redux";
import { authActions } from "../../redux/reducer/auth";

function NavBar() {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  // ly currentUser t localStorage
  const currentUser = localStorage.getItem("currentUser")
    ? JSON.parse(localStorage.getItem("currentUser"))
    : undefined;

  function handleLogout() {
    // nhn Logout
    dispatch(authActions.onLogout());
    navigate("/login");
  }

  return (
    <Card>
      <nav>
        <ul className={classes.NavBarList}>
          <div className={classes.NavBarLeft}>
            <li className={classes.NavBarHome}>
              <a
                //nhn vo nt Home chuyn sang trang HomePage
                onClick={() => {
                  navigate("/");
                }}
              >
                Home
              </a>
            </li>
            <li>
              <a
                //nhn vo nt Shop chuyn sang trang ShopPage
                onClick={() => {
                  navigate("/shop");
                }}
              >
                Shop
              </a>
            </li>
          </div>
          <li>
            <h2>BOUTIQUE</h2>
          </li>
          <div className={classes.NavBarRight}>
            <li>
              <a
                //nhn vo nt Cart chuyn sang trang CartPage
                onClick={() => {
                  navigate("/cart");
                }}
              >
                <ShoppingCartIcon sx={{ fontSize: 20 }} />
                Cart
              </a>
            </li>
            <li className={classes.LoginLogout}>
              <a
                //nhn vo nt Login chuyn sang trang LoginPage
                onClick={() => {
                  navigate("/login");
                }}
              >
                <PersonIcon sx={{ fontSize: 20 }} />
                {/* nu c currentUser th hin tn */}
                {currentUser ? `${currentUser.fullName}` : "Login"}
              </a>
              <div onClick={handleLogout} className={classes.Logout}>
                {/* nu c currentUser th hin nt Logout */}
                {currentUser ? "{ Logout }" : ""}
              </div>
            </li>
          </div>
        </ul>
      </nav>
    </Card>
  );
}

export default NavBar;
